import { computed, defineComponent, ref } from 'vue';
import { useStore } from 'vuex';
import { useRoute, useRouter } from 'vue-router';
import mapMenus from '@/utils/routeMapMenus';
export default defineComponent({
  name: 'NavMenu',
  props: {
    collapse: {
      type: Boolean,
      default: false
    }
  },

  setup() {
    const store = useStore();
    const router = useRouter();
    const route = useRoute();
    let menuId = ref('2'); //获取系统菜单

    const userMenus = computed(() => store.state.login.userMenus); //点击跳转路由

    const onClickItem = url => {
      const tempUrl = url.slice(5);
      const newUrl = `/home${tempUrl}`;
      router.push({
        path: newUrl
      });
    };

    menuId = mapMenus(userMenus.value, route.path);
    return {
      userMenus,
      onClickItem,
      menuId
    };
  }

});